<template>

    <v-btn v-if="!$slots['default'] && !items.length" @click="onClick" :title="$t(hint)" :disabled="disabled" text :class="['x-nav-button', classDevices]">
        <v-badge :value="!!alertText && !disabled" :content="alertText" :dot="!alertText && !alertIcon" :icon="alertIcon" :color="alertColor" :offset-y="!alertText && !alertIcon ? '5' : '11'">
            <slot v-if="!!$slots['icon']" name="icon"></slot>
            <v-icon v-else v-text="icon"></v-icon><span v-text="$t(label)" :class="{ 'ml-2': !!label }"></span>
        </v-badge>
    </v-btn>

    <v-menu v-else ref="menuRef" attach="header" bottom :left="left" offset-y :close-on-content-click="false" rounded="t-0" min-width="150" transition="fade-transition" content-class="x-nav-button">
        <template v-slot:activator="{ on, attrs }">
            <v-btn v-bind="attrs" v-on="on" :title="$t(hint)" @click="onClick" :disabled="disabled" text :class="[classDevices]">
                <v-badge :value="!!alertText && !disabled" :content="alertText" :dot="!alertText && !alertIcon" :icon="alertIcon" :color="alertColor" :offset-y="!alertText && !alertIcon ? '5' : '11'">
                    <slot v-if="!!$slots['icon']" name="icon"></slot>
                    <v-icon v-else v-text="icon"></v-icon><span v-text="$t(label)" :class="{ 'ml-2': !!label }"></span>
                </v-badge>
            </v-btn>
        </template>

        <v-card v-if="!!$slots['default']" tile>
            <slot name="default"></slot>
        </v-card>

        <v-list v-else dense class="pa-0">
            <v-sheet v-if="!!$slots['menu-panel']" class="x-menu-panel">
                <slot name="menu-panel"></slot>
            </v-sheet>

            <v-list-item v-for="(item, $index) in items" :key="$index" @click="onItemClick(item, $event)" :disabled="item.disabled" :title="$t(item.hint)" link :class="['x-menu-item', $vuetify.theme.isDark ? 'theme--dark' : 'theme--light', item.separator ? 'x-separator': '']">
                <v-list-item-icon v-if="item.icon" class="mt-1 mb-1 mr-2">
                    <v-icon v-text="item.icon" size="20"></v-icon>
                </v-list-item-icon>
                <v-list-item-content>
                    <v-list-item-title v-text="$t(item.label)"></v-list-item-title>
                </v-list-item-content>
            </v-list-item>
        </v-list>
    </v-menu>

</template>

<script>
export default {
    name: 'NavBtn',
    inheritAttrs: false,
    props: {
        label: {
            type: String,
        },
        hint: {
            type: String,
        },
        icon: {
            type: String,
            default: 'mdi-help-rhombus'
        },
        left: {
            type: Boolean,
            default: false
        },
        disabled: {
            type: Boolean,
            default: false
        },
        alertVisible: {
            type: Boolean
        },
        alertText: {
            type: String
        },
        alertIcon: {
            type: String
        },
        alertColor: {
            type: String,
            default: 'black'
        },
        classDevices: {
            type: String
        },
        item: {
            type: Object
        },
        items: {
            type: Array,
            default: () => []
        },
    },
    methods: {
        onClick($event) {
            this.$emit('click', $event);

            if (this.item && this.item.click) {
                this.item.click(this.item, $event);
            }

            if (this.item && this.item.to) {
                if (typeof this.item.to === 'object') {
                    return this.$router.push(this.item.to);
                }
                if (this.item.to.indexOf('/') > -1) {
                    return this.$router.push({ path: this.item.to });
                }
                this.$router.push({ name: this.item.to });
            }
        },

        onItemClick(item, $event) {
            if (this.$refs['menuRef']) this.$refs['menuRef'].isActive = false;

            if (item) {
                this.$emit('item-click', item, $event);

                if (item.click) {
                    item.click(item, $event);
                }

                if (item.to) {
                    if (typeof item.to === 'object') {
                        return this.$router.push(item.to);
                    }
                    if (item.to.indexOf('/') > -1) {
                        return this.$router.push({ path: item.to });
                    }
                    this.$router.push({ name: item.to });
                }
            }
        },
    },
}
</script>

<style scoped>
/* .x-nav-button .x-menu-item{
    min-height: 34px;
} */
.x-nav-button .x-menu-item.theme--light.x-separator{
    border-top: 1px solid rgba(0, 0, 0, 0.12);
}
.x-nav-button .x-menu-item.theme--dark.x-separator{
    border-top: 1px solid rgba(255, 255, 255, 0.12);
}
</style>
